// src/app/api/sendConfirmation/route.js
import { Resend } from "resend"
import { NextResponse } from "next/server"

// Initialize Resend with your API key stored in .env.local
const resend = new Resend(process.env.RESEND_API_KEY)

export async function POST(req) {
  try {
    const body = await req.json()
    const { customer, items, total, orderId, shippingAddress } = body

    if (!customer?.email) {
      return NextResponse.json(
        { error: "Missing customer email" },
        { status: 400 },
      )
    }

    // Build email HTML
    const html = `
      <div style="font-family:sans-serif;line-height:1.6;">
        <h2>Thank you for your order, ${customer.name}!</h2>
        <p>Your order ID: <strong>${orderId}</strong></p>
        <h3>Order Summary:</h3>
        <ul>
          ${items
            .map(
              (item) =>
                `<li>${item.name} x${
                  item.quantity
                } — $${item.price.toLocaleString()}</li>`,
            )
            .join("")}
        </ul>
        <p><strong>Total: $${total.toLocaleString()}</strong></p>
        <h3>Shipping Address:</h3>
        <p>
          ${shippingAddress.address}, ${shippingAddress.city}, ${
      shippingAddress.zip
    }, ${shippingAddress.country}
        </p>
        <p>We’ll start preparing your order right away.</p>
        <p>For support, contact <a href="mailto:support@yourdomain.com">support@yourdomain.com</a></p>
        <p><a href="https://yourdomain.com/order-confirmation">View your order</a></p>
      </div>
    `

    const data = await resend.emails.send({
      from: "Audiophile <onboarding@yourverifieddomain.com>", // must be verified
      to: customer.email, // <-- sends to whatever email user entered
      subject: "Your Audiophile Order Confirmation",
      html,
    })

    console.log("Email sent:", data)
    return NextResponse.json({ success: true })
  } catch (error) {
    console.error("Email sending error:", error)
    return NextResponse.json({ error: error.message }, { status: 500 })
  }
}
